.gnb-sub-wrap {
  position: relative;
  background: url('../images/gnb-sub-bg-02.png') no-repeat;
  background-size: cover;
  background-position: center center;
  height: 460px;
  min-width: 1200px;

  h1{
    position: absolute;
    top: calc(50% - 10px);
    left: 50%;
    transform: translate(-50%, -50%);
    @include ff(cb);
    font-size: 54px;
    color: #fff;
  }
  .gnb-sub{
    border-top: 1px solid #838f99;
    z-index: 1;
    position: absolute;
    bottom: 0;
    left: 0;
    // transform: translate(-50%, 0);
    width: 100%;

    .gnb-sub-list{
      max-width: 1200px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      li{
        margin: 0 30px;
        a{
          padding: 25px 95px;
          display: block;
          @include ff(cr);
          font-size: 18px;
          color: #fff;
          // border-top: 3px solid transparent;
          position: relative;
          &::before {
            position: absolute;
            left: 0;
            bottom: 67px;
            width: 100%;
            content: ' ';
            border: 3px solid #fff;
            transform: scaleX(0);
            transition: transform 250ms ease-in-out;
            // transform-origin: 0% 50%;
          }
          &:hover::before,
          &.active::before {
            transform: scaleX(1);
          }
        }
      }
    }
  }
}